                          const minValue = Math.min(...chartData);
                          const maxValue = Math.max(...chartData);
                          const range = maxValue - minValue || 1;
                          const width = 160;
                          const height = 48;
                          const isPositive = coin.price_change_percentage_24h >= 0;
                          const lineColor = isPositive ? '#10b981' : '#ef4444';
                          
